<template>
  <Modal
    :active="showModal"
    :event-name="'update-category-to-company-modal'"
    :full-screen="'mobile'"
    :custom-padding="'p-spacing-0'"
  >
    <UpdateCategoryToCompanyForm
      v-if="companyId"
      :company-name="companyName"
      :company-id="companyId"
      :products="products"
    />
  </Modal>
</template>

<script>
import Modal from "@/components/General/Modal.vue";
export default {
  components: {
    Modal,
    UpdateCategoryToCompanyForm: () =>
      import("@/components/Company/UpdateCategoryToCompanyForm.vue"),
  },
  data() {
    return {
      showModal: false,
      companyId: null,
      companyName: "",
      products: null,
    };
  },
  mounted() {
    this.$bus.$on("update-category-to-company-modal", (data) => {
      if (data?.showModal && data?.companyId) {
        this.showModal = data?.showModal;
        this.companyId = data?.companyId;
        this.companyName = data?.companyName;
        if (data?.products) {
          this.products = data?.products;
        }
      } else {
        this.showModal = false;
        this.companyId = null;
        this.companyName = "";
        this.products = null;
      }
    });
  },
  beforeDestroy() {
    this.$bus.$off("update-category-to-company-modal");
  },
};
</script>
